/* Nav */

	#nav {
		@include vendor('transition', (
			'background-color #{_duration(transition)} ease',
			'border-top-left-radius #{_duration(transition)} ease',
			'border-top-right-radius #{_duration(transition)} ease',
			'padding #{_duration(transition)} ease',
		));
		@include color-typography(invert);
		position: absolute;
		width: _size(inner);
		max-width: calc(100% - #{_size(element-margin) * 2});
		padding: 1em;
		background-color: _palette(invert, bg-alt);
		border-top-left-radius: _size(border-radius-main);
		border-top-right-radius: _size(border-radius-main);
		cursor: default;
		text-align: center;
		z-index: 1;

		& + #main {
			padding-top: 4.25em;
		}

		ul {
			margin: 0;
			padding: 0;
			list-style: none;

			li {
				@include vendor('transition', (
					'margin #{_duration(transition)} ease'
				));
				display: inline-block;
				margin: 0 0.35em;
				padding: 0;
				vertical-align: middle;

				a {
					@include vendor('transition', (
						'font-size #{_duration(transition)} ease'
					));
					display: inline-block;
					height: 2.25em;
					line-height: 2.25em;
					padding: 0 1.25em;
					border: 0;
					border-radius: _size(border-radius);
					box-shadow: inset 0 0 0 1px transparent;

					&:hover {
						background-color: _palette(invert, border-bg);
					}

					&.active {
						background-color: _palette(invert, bg);
						box-shadow: none;
					}
				}
			}
		}

		&.alt {
			position: fixed;
			top: 0;
			padding: 0.5em 1em;
			background-color: transparentize(_palette(invert, bg-alt), 0.05);
			border-top-left-radius: 0;
			border-top-right-radius: 0;
			z-index: _misc(z-index-base);

			ul {
				li {
					margin: 0 0.175em;

					a {
						font-size: 0.9em;
					}
				}
			}
		}

		#checkbox1, .toggle {
			display: none;
		}

		#checkbox1 + label {
			margin-bottom: initial;
			padding: 0;
			content: initial;
			a{
				color: #636363;
			}
			&:before{
				height: 0;
				width: 0;
				padding: 0;
				content: unset;
			}
		}

		@include breakpoint('<=small') {
			.toggle {
				clear: both;
				display: block;
				text-align: center;
				font-size: 14px;
				line-height: 40px;
				cursor: pointer;
				width: 100%;
				height: 40px;
				font-size: 18px;
				color: #595959;
				background: #dbdbdb;
				border-bottom-left-radius: 5px;
				border-bottom-right-radius: 5px;
				transition: all .1s linear;
			  }
			  .toggle:hover {
				background: #cecece;
			  }

			  #checkbox1 + label {
				width: 100%;
				margin-bottom: 0px;
			  }

			  #checkbox1:checked + label .demo li {
				opacity: 1;
				visibility: visible;
				transition: all .7s linear;
			  }

			  #checkbox1:checked + label .demo {
				height: 200px;
				
			  }

			  .menu {
				border-radius: 0px;
				border-top-left-radius: 5px;
				border-top-right-radius: 5px;
				display: flex;
				flex-direction: column;
				justify-content: space-around;
				align-items: center;
				height: 0px;
				transition: height .3s linear;
			  }

			  .menu li {
				display: flex;
				/* magic */
				align-self: center;
				width: 100%;
				opacity: 0;
				visibility: hidden;
			  }

			  .menu li a {
				width: 100%;
				text-align: center;
				align-self: center;
				align-content: center;
			  }
		}
	}